<template>
    <div id="blood-speciemen">
        <PageHeader :pageHead="name + '  (' + blood_type + ')'" />
        <div class="container" style="margin-top: 20px;">
            <div class="row">
                <div class="col s12">
                    <div class="card">
                        <div class="card-content white-text">
                        <span class="card-title black-text">Description</span>
                            <p class="black-text">
                                {{description}}
                            </p>
                            <div class="row">
                                <span class="badge red white-text">Quantity: <strong>{{quantity}}</strong></span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row">
                <table class="centered">
                    <thead>
                        <tr>
                            <th>Blood Type</th>
                            <th>Gives</th>
                            <th>Recieves</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>{{blood_type}}</td>
                            <td>
                                {{giveText}} 
                            </td>
                            <td>{{recieveText}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="row ">
                <router-link class="btn grey black-text" to="/blood-bank" style="margin-right: 10px;">Cancel</router-link>
                <router-link class="btn yellow black-text" :to="{name: 'edit-blood-specimen', params: { blood_id: blood_id }}">Edit</router-link>
            </div>
        </div>
    </div>
</template>

<script>
    import PageHeader from './PageHeader'
    import db from '../firebaseInit'
    export default {
        name: 'viewblood-speciemen',
        components: {
            PageHeader
        },
        data () {
            return {
                blood_id: "",
                blood_type: "",
                name: "",
                quantity: 0,
                description: "",
                gives: "",
                recieves: "",

                bloodSpecimen: []
            }
        },

        computed: {
            giveSpecimen() {
                return this.getBloodSpecimenByID(this.gives)
            },

            giveText() {
                let text = ""
                for (let i=0; i<this.giveSpecimen.length; i++) {
                    text += `${this.giveSpecimen[i].blood_type}${ (this.giveSpecimen.length>0 && i !== (this.giveSpecimen.length-1)) ? ', ' : ''}`;
                }
                return text === "" ? "---" : text.trim(",")
            },

            recieveText() {
                let text = ""
                for (let i=0; i<this.recieveSpecimen.length; i++) {
                    text += `${this.recieveSpecimen[i].blood_type}${ (this.recieveSpecimen.length>0 && i !== (this.recieveSpecimen.length-1)) ? ', ' : ''}`;
                }
                return text === "" ? "---" : text.trim(",")
            },

            recieveSpecimen() {
                return this.getBloodSpecimenByID(this.recieves)
            }
        },

        beforeRouteEnter(to, from, next) {
            db.collection("blood_specimen").where('blood_id', '==', to.params.blood_id).get()
                .then( querySnapshot => {
                    querySnapshot.forEach( doc => {
                        next(vm => {
                            vm.blood_id = doc.data().blood_id
                            vm.blood_type = doc.data().blood_type
                            vm.name = doc.data().name
                            vm.quantity = doc.data().quantity
                            vm.description = doc.data().description
                            vm.gives = doc.data().gives
                            vm.recieves = doc.data().recieves
                        })
                    })
                })
        }, 

        watch: {
            '$route': 'fetchData'
        }, 

        methods: {
            fetchData() {
                db.collection("blood_speciemen").where('blood_id', '==', this.$route.params.blood_id).get()
                    .then( querySnapshot => {
                        querySnapshot.forEach( doc => {
                            this.blood_id = doc.data().blood_id
                            this.blood_type = doc.data().blood_type
                            this.name = doc.data().name
                            this.quantity = doc.data().quantity
                            this.description = doc.data().description
                            this.gives = doc.data().gives
                            this.recieves = doc.data().recieves
                        })
                    })
            },

            getBloodSpecimenByID(idString) {
                const speciemen = []
                let ids = []

                try {
                    ids = idString.split(",")
                } catch (error) { console.log(error)}

                for (let i=0; i<ids.length; i++) {
                    this.bloodSpecimen.map( blood => {
                        if (blood.blood_id === ids[i].trim()) {
                            speciemen.push(blood)
                        }
                    })
                }

                return speciemen;
            }
        },

        created() {
            db.collection("blood_specimen").orderBy("name").get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        "id": doc.id,
                        "blood_id":  doc.data().blood_id,
                        "blood_type": doc.data().blood_type,
                        "name": doc.data().name,
                        "description": doc.data().description,
                        "quantity": doc.data().quantity,
                        "gives": doc.data().gives,
                        "recieves": doc.data().recieves,
                    }

                    this.bloodSpecimen.push(data)
                })
            })
        }
    }
</script>

<style scoped>
    .edit-blood-speciemen {
        width: 100% !important;

        display: flex;
        justify-content: center;
    }

    .frame-cell {
        max-width: 600px;
    }

    .frame-bg {
        background: #8a0303 !important;
    }
</style>