<template>
    <div id="login">
        <div class="login-panel">
            <div class="row">
               <div class="col s12">
                   <div class="login card-panel white-text center frame-bg">
                       <h5>Administrator Login</h5>
                       <form>
                           <div class="input-field">
                               <i class="material-icons prefix">email</i>
                               <input type="text" class="white-text" id="email" v-model="email">
                               <label class="white-text" for="email">Email</label>
                           </div>

                           <div class="input-field">
                               <i class="material-icons prefix">lock</i>
                               <input type="password" class="white-text" id="password" v-model="password">
                               <label class="white-text" for="password">Password</label>
                           </div>

                           <button @click="login" class="btn btn-medium lighten-4 white black-text">Login</button>
                       </form>
                   </div>
                   <div style="width: 100%; display: flex; justify-content: center; flex-direction: column;">
                       <div style="width: 100%; display:flex; justify-content: center;"><strong>Hint:</strong></div>
                       <div>email: <strong>administrator@bba.com</strong>&nbsp;&nbsp;&nbsp;password: <strong>administrator</strong></div>
                   </div>
               </div>
           </div>
        </div>
    </div>
</template>

<script>

    import firebase from 'firebase'

    export default {
        name: 'login',
        data () {
            return {
                email: "",
                password: ""
            }
        },

        computed: {
            
        },

        methods: {
            login(e) {
                e.preventDefault()
                firebase
                    .auth()
                    .signInWithEmailAndPassword(this.email, this.password)
                    .then( response => {
                        alert(`You are logged in as ${response.user.email}`)
                        this.$router.go({ path: this.$router.path })
                    },

                    err => alert(err.message))
            }
        }
    }
</script>

<style scoped>
    #login {
        width: 100%;
        display: flex;
        justify-content: center;

        margin-top: 80px;
    }

    .login-panel {
        max-width: 900px;
        min-height: 300px;
    }

    .frame-bg {
        background: #8a0303 !important;
    }
</style>