<template>
    <div id="blood-speciemen">
        <PageHeader :pageHead="pageHead" />
        <div class="edit-blood-speciemen">
            <div class="container">
                <div class="row">
                    <div class="col s8 offset-s2" style="margin-top: 30px;">
                        <div class="card-panel white-text center frame-bg">
                            <form>
                                <div class="input-field">
                                    <i class="material-icons prefix">vpn_key</i>
                                    <input type="text" disabled class="white-text center" id="blood-id" v-model="blood_id">
                                    <label v-if="!blood_id" class="white-text" for="blood-id">Blood ID</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">whatshot</i>
                                    <input type="text" class="white-text" id="blood-group" v-model="blood_type">
                                    <label v-if="!blood_type" class="white-text" for="blood-group">Blood Type</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">label_outline</i>
                                    <input type="text" class="white-text" id="label" v-model="name">
                                    <label v-if="!name" class="white-text" for="quantity">Name</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">lock</i>
                                    <input type="number" class="white-text" id="quantity" v-model="quantity">
                                    <label v-if="!quantity" class="white-text" for="quantity">Quantity</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">info_outline</i>
                                    <textarea id="description" v-model="description" class="materialize-textarea white-text"
                                        style="margin-bottom: 20px;"
                                    ></textarea>
                                    <label v-if="!description" class="white-text" for="description">Description</label>
                                </div>

                                <button @click="updateBloodSpeciemen" class="btn btn-medium lighten-4 white black-text">Update Record</button>
                            </form>
                        </div>
                        <div class="row" style="display: flex; justify-content: center;">
                            <router-link class="btn grey center" to="/blood-bank">Cancel</router-link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PageHeader from './PageHeader'
    import db from '../firebaseInit'
    export default {
        name: 'editblood-speciemen',
        components: {
            PageHeader
        },
        data () {
            return {
                pageHead: "Edit Blood Speciemen",
                blood_id: "",
                blood_type: "",
                name: "",
                quantity: 0,
                description: "",
                gives: "",
                recieves: "",

                bloodSpecimen: []
            }
        },

        beforeRouteEnter(to, from, next) {
            db.collection("blood_specimen").where('blood_id', '==', to.params.blood_id).get()
                .then( querySnapshot => {
                    querySnapshot.forEach( doc => {
                        next(vm => {
                            vm.blood_id = doc.data().blood_id
                            vm.blood_type = doc.data().blood_type
                            vm.name = doc.data().name
                            vm.quantity = doc.data().quantity
                            vm.description = doc.data().description
                            vm.gives = doc.data().gives
                            vm.recieves = doc.data().recieves
                        })
                    })
                })
        }, 

        watch: {
            '$route': 'fetchData'
        }, 

        methods: {
            fetchData() {
                db.collection("blood_speciemen").where('blood_id', '==', this.$route.params.blood_id).get()
                    .then( querySnapshot => {
                        querySnapshot.forEach( doc => {
                            this.blood_id = doc.data().blood_id
                            this.blood_type = doc.data().blood_type
                            this.name = doc.data().name
                            this.quantity = doc.data().quantity
                            this.description = doc.data().description
                            this.gives = doc.data().gives
                            this.recieves = doc.data().recieves
                        })
                    })
            },

            updateBloodSpeciemen() {
                console.log("Update Initiated");
                db.collection("blood_specimen").where('blood_id', '==', this.$route.params.blood_id).get()
                    .then( querySnapshot => {
                        querySnapshot.forEach( doc => {
                            doc.ref.update({
                                'blood_id': this.blood_id,
                                'blood_type': this.blood_type,
                                'name': this.name,
                                'quantity': this.quantity,
                                'description': this.description,
                            }).then(() => {
                                this.$router.push("/blood-bank")
                            })
                        })
                    })
            }
        },

        created() {
            db.collection("blood_specimen").orderBy("name").get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        "id": doc.id,
                        "blood_id":  doc.data().blood_id,
                        "blood_type": doc.data().blood_type,
                        "name": doc.data().name,
                        "description": doc.data().description,
                        "quantity": doc.data().quantity,
                        "gives": doc.data().gives,
                        "recieves": doc.data().recieves,
                    }

                    this.bloodSpecimen.push(data)
                })
            })
        }
    }
</script>

<style scoped>
    .edit-blood-speciemen {
        width: 100% !important;

        display: flex;
        justify-content: center;
    }

    .frame-cell {
        max-width: 800px;
    }

    .frame-bg {
        background: #8a0303 !important;
    }
</style>