<template>
    <nav id="navbar" class="nav-wrapper">
        <div class="container">
            <router-link to="/" class="brand-logo">BBA</router-link>
            <ul id="nav-mobile" class="right hide-on-med-and-down">
                <li v-if="isLoggedIn"><strong style="padding: 0 20px">{{ currentUser }}</strong></li>
                <li v-if="isLoggedIn"><button @click="logout" class="btn-small black lighten-5 white-text" style="text-transform: capitalize">Logout</button></li>
            </ul>
        </div>
    </nav>
</template>

<script>

    import firebase from 'firebase'

    export default {
        name: 'navbar',
        data () {
            return {
                isLoggedIn: false,
                currentUser: ''
            }
        },

        methods: {
            logout() {
                firebase
                    .auth()
                    .signOut()
                    .then(() => {
                        this.$router.go({ path: this.$router.path })
                    })
            }
        },

        created() {
            if (firebase.auth().currentUser) {
                this.isLoggedIn = true
                this.currentUser = firebase.auth().currentUser.email
            }
        }
    }
</script>

<style scoped>
    .nav-wrapper {
        background: #8a0303 !important;
        color: #fff !important;
    }
</style>