import Vue from 'vue'
import Router from 'vue-router'
import firebase from 'firebase'


import Dashboard from '../components/Dashboard'
import BloodBank from '../components/BloodBank'
import Login from '../components/Login'
import Register from '../components/Register'
import Transaction from '../components/Transaction'
import Enquiry from '../components/Enquiry'
import AddBloodGroup from '../components/AddBloodGroup'
import EditBloodGroup from '../components/EditBloodGroup'
import ViewBloodSpeciemen from '../components/ViewBloodSpeciemen'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },{
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        requiresGuest: true
      }
    },{
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },{
      path: '/blood-bank',
      name: 'blood-bank',
      component: BloodBank,
      meta: {
        requiresAuth: true
      }
    },{
      path: '/blood-bank/add',
      name: 'add-blood-specimen',
      component: AddBloodGroup,
      meta: {
        requiresAuth: true
      }
    },{
      path: '/blood-bank/edit/:blood_id',
      name: 'edit-blood-specimen',
      component: EditBloodGroup,
      meta: {
        requiresAuth: true
      }
    },{
      path: '/blood-bank/:blood_id',
      name: 'view-blood-specimen',
      component: ViewBloodSpeciemen,
      meta: {
        requiresAuth: true
      }
    },{
      path: '/transactions',
      name: 'transactions',
      component: Transaction,
      meta: {
        requiresAuth: true
      }
    },{
      path: '/orders',
      name: 'orders',
      component: Enquiry,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!firebase.auth().currentUser) {
            next({
                path: '/login',
                query: {
                    redirect: to.fullPath
                }
            })
        } else {
            next()
        }
    } else if (to.matched.some(record => record.meta.requiresGuest)) {
        if (firebase.auth().currentUser) {
          next({
              path: '/',
              query: {
                  redirect: to.fullPath
              }
          })
        } else {
            next()
        }
    } else {
        next()
    }
})

export default router;