<template>
    <div id="blood-bank">
        <PageHeader :pageHead="pageHead" />
        <table class="container">
            <thead>
            <tr>
                <th>#</th>
                <th>Blood Type</th>
                <th>Quantity</th>
                <th>Actions</th>
            </tr>
            </thead>

            <tbody>
            <tr
                v-for="(blood, index) in bloodSpecimen"
                :key="blood.id"
            >
                <td>{{index+1}}</td>
                <td><h5>{{blood.name}} ({{blood.blood_type}}) </h5></td>
                <td>{{blood.quantity}}</td>
                <td>
                    <router-link 
                        :to="{ 
                            name: 'view-blood-specimen',
                            params: {blood_id: blood.blood_id}
                        }"
                    >
                        <i class="material-icons green-text small">remove_red_eye</i>
                    </router-link>
                </td>
            </tr>
            </tbody>
        </table>
        <div class="fixed-action-btn">
            <router-link to="/blood-bank/add" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import PageHeader from './PageHeader'
    import db from '../firebaseInit'
    export default {
        name: 'blood-bank',
        components: {
            PageHeader
        },
        data () {
            return {
                pageHead: "Blood Bank",
                bloodSpecimen: []
            }
        },

        created() {
            db.collection("blood_specimen").orderBy("name").get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        "id": doc.id,
                        "blood_id":  doc.data().blood_id,
                        "blood_type": doc.data().blood_type,
                        "name": doc.data().name,
                        "description": doc.data().description,
                        "quantity": doc.data().quantity,
                        "gives": doc.data().gives,
                        "recieves": doc.data().recieves,
                    }

                    this.bloodSpecimen.push(data)
                })
            })
        }
    }
</script>

<style scoped>
    .blood-details {
        width: 100%;
    }

    .desc {
        font-size: 80%;
        height: 40px;
    }

    td {
        height: 80px !important;
        padding-bottom: 0 !important;
    }

    td .row {
        margin-bottom: 5px;
    }
</style>