import Vue from 'vue'
import App from './App.vue'
import router from './router'
import UUID from "vue-uuid";

import firebase from 'firebase'
import './firebaseInit'

Vue.config.productionTip = false
Vue.use(UUID);

let app

firebase.auth().onAuthStateChanged( () => {
  if (!app) {
    app = new Vue({
      router,
      render: h => h(App),
    }).$mount('#app')
  }
})



