<template>
    <div id="blood-speciemen">
        <PageHeader :pageHead="pageHead" />
        <div class="add-blood-speciemen">
            <div class="container">
                <div class="row">
                    <div class="col s8 offset-s2" style="margin-top: 30px;">
                        <div class="card-panel white-text center frame-bg">
                            <form>
                                <div class="input-field">
                                    <i class="material-icons prefix">vpn_key</i>
                                    <input type="text" disabled class="white-text center" id="blood-id" required v-model="blood_id">
                                    <label v-if="!blood_id" class="white-text" for="blood-id">Blood ID</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">whatshot</i>
                                    <input type="text" class="white-text" id="blood-group" required v-model="blood_type">
                                    <label v-if="!blood_type" class="white-text" for="blood-group">Blood Type</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">label_outline</i>
                                    <input type="text" class="white-text" required v-model="name">
                                    <label v-if="!name" class="white-text" for="quantity">Name</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">lock</i>
                                    <input type="number" class="white-text" id="quantity" required v-model="quantity">
                                    <label v-if="!quantity" class="white-text" for="quantity">Quantity</label>
                                </div>

                                <div class="input-field">
                                    <i class="material-icons prefix">info_outline</i>
                                    <textarea id="description" v-model="description" required class="materialize-textarea white-text"
                                        style="margin-bottom: 20px;"
                                    ></textarea>
                                    <label v-if="!description" class="white-text" for="description">Description</label>
                                </div>

                                <button @click="AddloodSpeciemenRecord" class="btn btn-medium lighten-4 white black-text">Add Blood Record</button>
                            </form>
                        </div>
                        <div class="row" style="display: flex; justify-content: center;">
                            <router-link class="btn grey center" to="/blood-bank">Cancel</router-link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PageHeader from './PageHeader'
    import db from '../firebaseInit'

    // import { uuid } from 'vue-uuid';

    export default {
        name: 'addblood-speciemen',
        components: {
            PageHeader
        },
        data () {
            return {
                pageHead: "Add Blood Speciemen",
                blood_id: "",
                blood_type: "",
                name: "",
                quantity: null,
                description: "",
                gives: "",
                recieves: "",
            }
        },

        methods: {
            AddloodSpeciemenRecord () {
                if(this.blood_id) {
                    db.collection("blood_specimen").add({
                        "blood_id":  this.blood_id,
                        "blood_type": this.blood_type,
                        "name": this.name,
                        "description": this.description,
                        "quantity": this.quantity
                    }).then( () => {
                        // console.log(docRef)
                        alert("Blood Speciemen added successfully.")
                        this.$router.push("/blood-bank")
                    })
                    .catch( error => console.log(error))
                } else {
                    alert("Blood ID generation Error: Please try reloading this page")
                }
            }

        },

        created() {
            this.blood_id = `${this.$uuid.v1()}`
        }
    }
</script>

