<template>
    <div id="blood-bank">
        <PageHeader :pageHead="pageHead" />
        <table class="container">
            <thead>
            <tr>
                <th>#</th>
                <th>Blood Type</th>
                <th>Quantity</th>
                <th>Actions</th>
            </tr>
            </thead>

            <tbody>
            <tr
                v-for="(blood, index) in orders"
                :key="blood.id"
            >
                <td>{{index+1}}</td>
                <td><h5>Test </h5></td>
                <td>test</td>
                <td>
                    <!-- <router-link 
                        :to="{ 
                            name: 'view-blood-specimen',
                            params: {blood_id: blood.blood_id}
                        }"
                    >
                        <i class="material-icons green-text small">remove_red_eye</i>
                    </router-link> -->
                </td>
            </tr>
            </tbody>
        </table>
        <div class="fixed-action-btn">
            <router-link to="/blood-bank/add" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import PageHeader from './PageHeader'
    import db from '../firebaseInit'
    export default {
        name: 'orders',
        components: {
            PageHeader
        },
        data () {
            return {
                pageHead: "Enquiries",
                orders: [],
                users: [],
                status: []
            }
        },

        created() {
            db.collection("orders").orderBy("order_date").get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        "id": doc.id,
                        "order_id":  doc.data().order_id,
                        "order_date": doc.data().order_date,
                        "order_by": doc.data().order_by,
                        "quantity": doc.data().quantity,
                        "status": doc.data().status,
                    }

                    this.orders.push(data)
                })
            }) 
            
            db.collection("users").orderBy("fullname").get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        "id": doc.id,
                        "fullname":  doc.data().fullname,
                        "blood_group": doc.data().blood_group,
                        "contact": doc.data().contact,
                        "address": doc.data().address,
                        "user_id": doc.data().user_id,
                        "donor": doc.data().donor
                    }

                    this.users.push(data)
                })
            })

            db.collection("order_status").orderBy("name").get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        "id": doc.id,
                        "status_id":  doc.data().status_id,
                        "name": doc.data().name
                    }

                    this.status.push(data)
                })
            })
        }
    }
</script>

