<template>
    <div id="login">
        <div class="login-panel">
            <div class="row">
               <div class="col s12">
                   <div class="login card-panel white-text center login-bg">
                       <h5>Register</h5>
                       <form>
                           <div class="input-field">
                               <i class="material-icons prefix">email</i>
                               <input type="text" id="email" v-model="email" required>
                               <label class="white-text" for="email">Email</label>
                           </div>

                           <div class="input-field">
                               <i class="material-icons prefix">lock</i>
                               <input type="password" id="password" v-model="password" required>
                               <label class="white-text" for="password">Password</label>
                           </div>

                           <button @click="register" class="btn btn-medium lighten-4 white black-text">Register</button>
                       </form>
                   </div>
               </div>
           </div>
        </div>
    </div>
</template>

<script>

    import firebase from 'firebase'

    export default {
        name: 'register',
        data () {
            return {
                email: "",
                password: ""
            }
        },

        methods: {
            register(e) {
                e.preventDefault()
                firebase
                    .auth()
                    .createUserWithEmailAndPassword(this.email, this.password)
                    .then( response => {
                        alert(`Account created for ${response.user.email}`)
                        this.$router.go({ path: this.$router.path })
                    },
                    
                    err => alert(err.message))
            }
        }
    }
</script>

<style scoped>
    #login {
        width: 100%;
        display: flex;
        justify-content: center;

        margin-top: 80px;
    }

    .login-panel {
        max-width: 900px;
        min-height: 300px;
    }

    .login-bg {
        background: #8a0303 !important;
    }
</style>